.Root{
  text-align: center;
  font-family: 'Balsamiq Sans', cursive;
  background-color: rgb(194, 193, 193);
}

.MainContent{
  display: inline-block;
  width: 90%;
  background-color: antiquewhite;
  background-image: url("http://images.pexels.com/photos/934062/pexels-photo-934062.jpeg");
  
  /* Add the blur effect */
  

  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;

  height:670px;
  margin-top:-10px;
}


#Vehicle1,#Vehicle2,#Vehicle3,#Vehicle4{
  display:none;
}

.DestinationLists{
  display:-ms-flexbox;
  display:flex;
}
.Planet{
  width:25%;
  
  text-align: left;
  
  background-color: white;
  margin-top: 30px;
  margin: 15px;
  padding-top: 10px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 30px;

  border-radius: 5px;
  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

p,select{
  margin-left: 10px;
}

p{
  font-size: 16px;
}

radio{
  margin-right: 5px;
}

select{
   width: 150px;
   height: 25px;
   border-radius: 5px;
   -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
           box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}


.Menu-style {
  overflow: hidden;
  background-color: rgba(85, 66, 25, 0.87);
  background-size: cover;
  display: inline-block;
  width: 100%;
  margin-bottom: 0px;
  position: -webkit-sticky;
  top: 0;

  -webkit-box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);

          box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

.Menu-style a {
  float: left;
  color: #f2f2f2;
  text-align: center;
  padding: 10px 29px;
  text-decoration: none;
  font-size: 22px;
  font-family: 'Lobster Two', cursive;
}

.Menu-style a:hover {
  background-color: rgb(22, 22, 22);
  color: rgb(210, 228, 49);
}

footer{
  height: 70px;
  color: white;
  padding: 23px 25px;
  background-color: rgba(14, 110, 43, 0.89);

  background-size: cover;
  width: 90%;
  display: inline-block;
}

.Footer{
  text-align: center;
}

.menu{
  width: 25px;
  height: 3px;
  background-color: black;
  margin: 4px 0;
}

.MenuButton{
  margin:10px 15px;
  margin-top: 19px;
  display: none;
}

.collapsemenu {
  margin-top:-5px;
  overflow: hidden;
  position: relative;
  text-align: center;
  background-color: rgba(85, 66, 26, 0.87);
}

.collapsemenu #menuLinks {
  display: inline-block;
  width:100%;
}

.collapsemenu a {
  color: white;
  padding: 9px 16px;
  text-decoration: none;
  font-size: 17px;
  display: block;
}


.collapsemenu a:hover {
  color: rgb(210, 228, 49);
}

h4{
  padding-top: 13px;
  padding-bottom: 10px;
}

.Time{
  margin-top: 25px;
  margin-bottom: 25px;
}

.ResultLabel{
  margin-bottom: 25px;
}

.AvailableVehicles{
  margin-top: 15px;
}

.ButtonFindFalcone{
  margin-bottom: 25px;
}

.BoundaryErrMsg{
  max-width: 300px;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@media only screen and (max-width: 1030px) {
    .MenuButton{
      display:block;
    }
    .Menu-style a{
      display:none;
    }
    .Menu-style a:hover{
      display:none;
    }
    .DestinationLists{
      -ms-flex-direction: column;
          flex-direction: column;
    }
    .Planet{
      width:92%;
    }
    .MainContent{
      height:100%;
    }
    .BoundaryErrMsg{
      max-width: 90%;
    }
    
}



